import React, { FC, useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { useDispatch, useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Card, CardBody, CardTitle, Col, CustomInput, FormGroup, Row } from 'reactstrap';
import {
  EditForm,
  EditViewProps,
  getSelectedItem,
  InputGroupInline,
  InputTooltipProps,
  InputType,
  isEmpty,
  WindowComponent,
  withTooltip,
} from 'summer';
import { CLASS_NAME, LancamentoMCGConfig } from '../../../../models/dtos/mcg/lancamentoMCGConfig';
import { LANCAMENTO_CONFIG_HISTORICO_TYPE_ENUM_VALUES, LANCAMENTO_UNICO } from '../../../../models/enumerated/mcg/lancamentoConfigHistoricoTypeEnum';
import { LANCAMENTO_MCG_FORMA_ENUM_VALUES, UM_DEBITO_UM_CREDITO } from '../../../../models/enumerated/mcg/lancamentoMCGFormaEnum';
import { LANCAMENTO_MCG_TIPO_VALID_LANC_PARTIDA_SIMPLES_ENUM_VALUES } from '../../../../models/enumerated/mcg/lancamentoMCGTipoValidLancPartidaSimplesEnum';
import { APENAS_REF_ATUAL, TIPO_BLOQUEIO_LANCAMENTO_ENUM_VALUES } from '../../../../models/enumerated/mcg/tipoBloqueioLancamentoEnum';
import { Reducers } from '../../../../store/ducks';
import { GlobalState, setGlobalParameter } from '../../../../store/ducks/global.duck';

interface LancamentoMCGConfigEditViewProps extends EditViewProps<LancamentoMCGConfig> {}

const LancamentoMCGConfigEditView: FC<LancamentoMCGConfigEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  const dispatch = useDispatch();

  const formProps = useForm<any>({ defaultValues: selectedItem, mode: 'onBlur' });

  const [disCentroCustoAtivoPassivo, setDisCentroCustoAtivoPassivo] = useState(false);

  setTimeout(() => formProps.triggerValidation('historico'), 800);

  useEffect(
    () => {
      if (isEmpty(formProps.getValues().id)) {
        setTimeout(() => {
          formProps.setValue('exibirSaldoContas', true);
          formProps.setValue('fixarData', true);
          formProps.setValue('utilizarHistoricoVariavel', true);
          formProps.setValue('utilizarHistoricoPadrao', true);
          formProps.setValue('utilizarCentroCusto', false);
          formProps.setValue('formaLancamento', UM_DEBITO_UM_CREDITO.value, true);
          formProps.setValue('historico', LANCAMENTO_UNICO.value, true);
          formProps.setValue('tipoBloqueioLancamento', APENAS_REF_ATUAL.value, true);
        }, 500);
      }
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  useEffect(
    () => {
      setTimeout(() => {
        changeUtilizarCentroCusto();
      }, 700);
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const validateExtras = (formData: LancamentoMCGConfig): string[] => {
    const erros = [];
    if (!formData.utilizarHistoricoVariavel && !formData.utilizarHistoricoPadrao) {
      erros.push('Pelo menos um tipo de histórico deve ser utilizado.');
    }
    return erros;
  };

  const afterPersist = lancamentoMCGConfig => {
    if (globalReducer.globalParameter.lancamentoMCGConfig.id === lancamentoMCGConfig.id) {
      dispatch(
        setGlobalParameter({
          ...globalReducer.globalParameter,
          lancamentoMCGConfig,
        })
      );
    }
  };

  const marcarTodos = (): void => {
    if (formProps.getValues().marcarTodos) {
      formProps.setValue('fixarData', true, true);
      formProps.setValue('fixarContaDebito', true, true);
      formProps.setValue('fixarContaCredito', true, true);
      formProps.setValue('fixarCentroCustoDebito', true, true);
      formProps.setValue('fixarCentroCustoCredito', true, true);
      formProps.setValue('fixarHistoricoPadrao', true, true);
      formProps.setValue('fixarHistoricoVariavel', true, true);
    } else {
      formProps.setValue('fixarData', false, true);
      formProps.setValue('fixarContaDebito', false, true);
      formProps.setValue('fixarContaCredito', false, true);
      formProps.setValue('fixarCentroCustoDebito', false, true);
      formProps.setValue('fixarCentroCustoCredito', false, true);
      formProps.setValue('fixarHistoricoPadrao', false, true);
      formProps.setValue('fixarHistoricoVariavel', false, true);
    }
  };

  const propToolTip = (messagem: string): InputTooltipProps => {
    return {
      message: messagem,
      placement: 'top-start',
    };
  };

  const changeUtilizarCentroCusto = () => {
    const values = formProps.getValues();
    setDisCentroCustoAtivoPassivo(!values.utilizarCentroCusto);
    if (!values.utilizarCentroCusto) {
      formProps.setValue('utilizarCentroCustoAtivoPassivo', false);
    }
  };

  return (
    <WindowComponent title="Manutenção de Configuração de Lançamento" useGlobalParameter={true} globalParameter={globalReducer.globalParameter}>
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME} validateExtras={validateExtras} afterPersist={afterPersist}>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { maxLength: 20, required: true } }}
              id="descricao"
              isPrependLabel={true}
              label="Descrição"
              placeholder="Informe a Descrição"
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <Card style={{ marginBottom: '16px', borderWidth: 'thin' }}>
              <CardBody>
                <CardTitle>Gerais</CardTitle>
                <FormGroup tag={Row}>
                  <Col>
                    <CustomInput
                      id="idexibirSaldoContas"
                      innerRef={formProps.register}
                      label="Exibir Saldo das Contas"
                      name="exibirSaldoContas"
                      type="switch"
                    />
                  </Col>
                  <Col>
                    <CustomInput
                      id="idvalidarLancReceitaDespesa"
                      innerRef={formProps.register}
                      label="Validar lançamento de Receita e Despesa"
                      name="validarLancReceitaDespesa"
                      type="switch"
                    />
                  </Col>
                </FormGroup>
                <FormGroup tag={Row}>
                  <Col>
                    <CustomInput
                      id="idutilizarLancPadrao"
                      innerRef={formProps.register}
                      label="Utilizar lançamento Padrão"
                      name="utilizarLancPadrao"
                      type="switch"
                    />
                  </Col>
                  <Col>
                    <CustomInput
                      id="idinformarLancamentoEncerramento"
                      innerRef={formProps.register}
                      label="Informar lançamento de Encerramento"
                      name="informarLancamentoEncerramento"
                      type="switch"
                    />
                  </Col>
                </FormGroup>
              </CardBody>
            </Card>
          </Col>
        </Row>
        <Row>
          <Col>
            <Card style={{ marginBottom: '16px', borderWidth: 'thin' }}>
              <CardBody>
                <CardTitle>Histórico</CardTitle>
                <FormGroup tag={Row}>
                  <Col>
                    <CustomInput
                      id="idutilizarHistoricoPadrao"
                      innerRef={formProps.register}
                      label="Utilizar Histórico Padrão"
                      name="utilizarHistoricoPadrao"
                      type="switch"
                    />
                  </Col>
                </FormGroup>
                <FormGroup tag={Row}>
                  <Col>
                    <CustomInput
                      id="idutilizarHistoricoVariavel"
                      innerRef={formProps.register}
                      label="Utilizar Histórico Variável"
                      name="utilizarHistoricoVariavel"
                      type="switch"
                    />
                  </Col>
                </FormGroup>
              </CardBody>
            </Card>
          </Col>
          <Col>
            <Card style={{ marginBottom: '16px', borderWidth: 'thin' }}>
              <CardBody>
                <CardTitle>Centro de Custo</CardTitle>
                <FormGroup tag={Row}>
                  <Col>
                    <CustomInput
                      id="utilizarCentroCusto"
                      innerRef={formProps.register}
                      label="Utilizar Centro de Custo"
                      name="utilizarCentroCusto"
                      type="switch"
                      onChange={changeUtilizarCentroCusto}
                    />
                  </Col>
                </FormGroup>
                <FormGroup tag={Row}>
                  <Col>
                    <CustomInput
                      disabled={disCentroCustoAtivoPassivo}
                      id="idutilizarCentroCustoAtivoPassivo"
                      innerRef={formProps.register}
                      label="Utilizar Centro de Custo para Ativo e Passivo"
                      name="utilizarCentroCustoAtivoPassivo"
                      type="switch"
                    />
                    {withTooltip(
                      propToolTip('Permite o lançamento de Centro de Custo para Contas do Ativo e Passivo'),
                      'utilizarCentroCustoAtivoPassivo'
                    )}
                  </Col>
                </FormGroup>
              </CardBody>
            </Card>
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true } }}
              id="formaLancamento"
              inputDropDownProps={{ optionsProvider: LANCAMENTO_MCG_FORMA_ENUM_VALUES }}
              isPrependLabel={true}
              label="Fórmula"
              placeholder="Informe o Tipo de Fórmula"
              type={InputType.DROP_DOWN}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true } }}
              id="historico"
              inputDropDownProps={{ optionsProvider: LANCAMENTO_CONFIG_HISTORICO_TYPE_ENUM_VALUES }}
              isPrependLabel={true}
              label="Histórico"
              placeholder="Informe o Tipo de Histórico"
              type={InputType.DROP_DOWN}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true } }}
              id="tipoBloqueioLancamento"
              inputDropDownProps={{ optionsProvider: TIPO_BLOQUEIO_LANCAMENTO_ENUM_VALUES }}
              isPrependLabel={true}
              label="Permitir Lançamento"
              placeholder="Informe o Tipo de Permissão Lançamento"
              type={InputType.DROP_DOWN}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true } }}
              id="validacaoLancPartidaSimples"
              inputDropDownProps={{ optionsProvider: LANCAMENTO_MCG_TIPO_VALID_LANC_PARTIDA_SIMPLES_ENUM_VALUES }}
              isPrependLabel={true}
              label="Permitir Lançamento Partida Simples"
              placeholder="Informe o Tipo de Permissão Lançamento de Partida Simples"
              type={InputType.DROP_DOWN}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <Card style={{ marginBottom: '16px', borderWidth: 'thin' }}>
              <CardBody>
                <CardTitle>Habilitar a Fixação dos Campos no Lançamento</CardTitle>
                <FormGroup tag={Row}>
                  <Col>
                    <CustomInput id="idfixarData" innerRef={formProps.register} label="Data" name="fixarData" type="switch" />
                  </Col>
                  <Col>
                    <CustomInput id="idfixarContaDebito" innerRef={formProps.register} label="Conta Débito" name="fixarContaDebito" type="switch" />
                  </Col>
                </FormGroup>
                <FormGroup tag={Row}>
                  <Col>
                    <CustomInput
                      id="idfixarContaCredito"
                      innerRef={formProps.register}
                      label="Conta Crédito"
                      name="fixarContaCredito"
                      type="switch"
                    />
                  </Col>
                  <Col>
                    <CustomInput
                      id="idfixarCentroCustoDebito"
                      innerRef={formProps.register}
                      label="Centro de Custo Débito"
                      name="fixarCentroCustoDebito"
                      type="switch"
                    />
                  </Col>
                </FormGroup>
                <FormGroup tag={Row}>
                  <Col>
                    <CustomInput
                      id="idfixarCentroCustoCredito"
                      innerRef={formProps.register}
                      label="Centro de Custo Crédito"
                      name="fixarCentroCustoCredito"
                      type="switch"
                    />
                  </Col>
                  <Col>
                    <CustomInput
                      id="idfixarHistoricoPadrao"
                      innerRef={formProps.register}
                      label="Histórico Padrão"
                      name="fixarHistoricoPadrao"
                      type="switch"
                    />
                  </Col>
                </FormGroup>
                <FormGroup tag={Row}>
                  <Col>
                    <CustomInput
                      id="idfixarHistoricoVariavel"
                      innerRef={formProps.register}
                      label="Histórico Variável"
                      name="fixarHistoricoVariavel"
                      type="switch"
                    />
                  </Col>
                  <Col>
                    <CustomInput
                      id="marcarTodos"
                      innerRef={formProps.register}
                      label="Marcar/Desmarcar Todos"
                      name="marcarTodos"
                      type="switch"
                      onChange={marcarTodos}
                    />
                  </Col>
                </FormGroup>
              </CardBody>
            </Card>
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default LancamentoMCGConfigEditView;
